import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DalService } from '../../services/dal.service';
import { SeoService } from '../../services/seo.service';
import { IMService, IMEventTypes, NotifTypes } from '../../services/im.service';
import { SharingProvider } from '../../services/social-share.service';

@Component({
  selector: 'app-article-series-details',
  templateUrl: './article-series-details.component.html',
  styleUrls: ['./article-series-details.component.css']
})
export class ArticleSeriesDetailsComponent implements OnInit {
  blogLink = null;
  blogData: any = ""
  article: any = {}
  commentModel: any = {
    name: '',
    email: '',
    comment: ''
  }
  comments = [];
  constructor(private route: ActivatedRoute, private seo: SeoService, private dal: DalService, private im: IMService) { }

  ngOnInit() {

    this.route.params.subscribe(params => {
      const { id, seriesName, articleName } = params;
      this.blogLink = '/series/article/' + seriesName + '/' + articleName + '/' + id
      this.dal.getMdFile(this.blogLink).subscribe(x => {

        x = x[0];

        this.article = x;
        // this.article.authorDetails.profile = 'https://res.cloudinary.com/brainethic/image/upload/c_scale,w_245/v1571636313/IMG_1022_aamuxq.jpg'
        this.seo.setMDFileSEO(x)
        this.commentModel.articleId = x._id
        this.fetchComments();
        if (this.article.authorId) {
          this.dal.getUserDetails({ userId: this.article.authorId }).subscribe(x => {
            x = x && x[0];
            this.article.authorDetails = x.profile
          })
        }
      }, e => {
        console.log(e)
      })
    });
  }

  shareArticle(type) {
    this.dal.articleStats(this.article._id, 'shares').subscribe(x => {
      console.log('Share incremented', x);
    })
    this.seo.socialShareService.share(type, this.article)
  }

  fetchComments() {
    this.dal.getComments({ articleId: this.commentModel.articleId }).subscribe(y => {
      this.comments = y;
    })
  }

  saveComment() {
    this.commentModel.postedOn = new Date().getTime();
    if (this.commentModel.name.trim().length < 3 || this.commentModel.email.trim().length < 10 || this.commentModel.comment.trim().length < 20) {
      this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Please fill name (min 3 char), email (valid) and comment (min 20 char) properly', type: NotifTypes.ERROR })
    } else {
      this.dal.postComment(this.commentModel).subscribe(x => {
        console.log(x);
        if (x._id) {
          this.commentModel.name = '';
          this.commentModel.email = '';
          this.commentModel.comment = '';

          this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Thank you for your comment', type: NotifTypes.SUCCESS })
        }
        this.fetchComments();
      })
    }

  }

}
