import { Component, OnInit } from '@angular/core';
import { DalService } from '../../services/dal.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-article-series-list',
  templateUrl: './article-series-list.component.html',
  styleUrls: ['./article-series-list.component.css']
})
export class ArticleSeriesListComponent implements OnInit {
  header = {
    title: 'Blogs and Article Series',
    details: 'Learn technology in step by step with Article Series',
    imageUrl: 'https://images.unsplash.com/photo-1472745433479-4556f22e32c2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80',
    overlay: true,
    playBtn: false,
    courseLink: null
  }
  activeId = null;
  articles = [];
  seriesList = [];
  matchedArticleCount = -1;
  searchVisible = false;
  filteredArticles = null;
  constructor(private dal: DalService, private route: ActivatedRoute) { }

  ngOnInit() {

    this.dal.getSeriesList().subscribe(x => {
      this.seriesList = x;
    })
    this.dal.getArticles({ "status": "PUBLISHED", skipFields: 'blogData,authorDetails' }).subscribe(x => {
      this.articles = x;
      this.route.params.subscribe(params => {
        const { seriesName } = params;
        this.activeId = seriesName;
        this.matchedArticleCount = this.articles.filter(x => x.seriesType === seriesName).length;
      });
    })
  }
  searchArticle(searchQuery) {
    this.dal.searchArticle(searchQuery).subscribe((x) => {
      this.filteredArticles = x;
      console.log(x)

    })
  }
}
