import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ArticleSeriesListComponent } from '../../components/article-series-list/article-series-list.component';
import { ArticleSeriesDetailsComponent } from '../../components/article-series-details/article-series-details.component';


const routes: Routes = [
  {
    path: "list", component: ArticleSeriesListComponent
  },
  {
    path: "list/:seriesName", component: ArticleSeriesListComponent
  },
  {
    path: "article/:seriesName/:articleName/:id", component: ArticleSeriesDetailsComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ArticleseriesRoutingModule { }
